"use client";

import React, { useEffect } from "react";
import { Box, Typography, Button, Card, CardContent } from "@mui/material";
import { userStore } from "@/store/userStore";
import { useRouter } from "next/navigation";
import Link from "next/link";

function DashboardPage() {
  const { user, CheckUser } = userStore();
  const router = useRouter();

  useEffect(() => {
    CheckUser();
  }, [CheckUser]);

  if (!user) {
    router.push("/user/login");
    return null;
  }

  return (
    <div className="flex flex-col items-center justify-center min-h-[90vh] bg-[#112330] text-white px-4">
      <Typography
        variant="h3"
        className="text-green-400 mb-8 font-bold text-center"
      >
        Welcome, {user.name} üëã
      </Typography>

      <Card
        sx={{
          bgcolor: "#1c394c",
          color: "#fff",
          border: "1px solid #5bcfb1",
          borderRadius: 2,
          boxShadow: "0 4px 20px rgba(91,207,177,0.3)",
          padding: 4,
          width: "100%",
          maxWidth: 400,
        }}
      >
        <CardContent className="flex flex-col items-center space-y-6">
          {/* Create Poll */}
          <div className="flex flex-col items-center space-y-2 w-full">
            <Typography variant="h5">Create Poll</Typography>
            <Button
              variant="contained"
              sx={{ backgroundColor: "#5bcfb1", color: "#112330" }}
              component={Link}
              href="/create"
              fullWidth
            >
              Go to Create
            </Button>
          </div>

          {/* My Polls (text only, no button) */}
          <div className="flex flex-col items-center space-y-2 w-full">
            <Typography variant="h5">My Polls</Typography>
            <Typography variant="body2" sx={{ color: "#ccc" }}>
              View your polls from the menu.
            </Typography>
          </div>

          {/* Logout */}
          <div className="flex flex-col items-center space-y-2 w-full">
            <Typography variant="h5">Account</Typography>
            <Button
              variant="contained"
              sx={{ backgroundColor: "#e63946", color: "#fff" }}
              onClick={() => {
                localStorage.removeItem("token");
                router.push("/user/login");
              }}
              fullWidth
            >
              Logout
            </Button>
          </div>
        </CardContent>
      </Card>

      <Typography
        variant="body2"
        textAlign="center"
        sx={{ color: "#ccc", marginTop: 6 }}
      >
        Built with ‚ù§Ô∏è using Next.js, MUI, and Tailwind
      </Typography>
    </div>
  );
}

export default DashboardPage;
